import {
    elizaLogger,
    Action,
    ActionExample,
    HandlerCallback,
    IAgentRuntime,
    Memory,
    State,
} from "@elizaos/core";
import { validateCookieConfig } from "../environment";
import {getTrendingAgentsExamples} from "../examples";
import { createCookieService } from "../services";
import {TrendingAgentsResponse} from "../types.ts";

const formatNumber = (value: number | null): string => {
    if (value === null || typeof value !== 'number') {
        return 'N/A';
    }
    return value.toFixed(2);
};

const formatAgentMetrics = (agent: TrendingAgentsResponse): string => {
    const twitterHandle = agent.twitterUsernames?.[0] || 'N/A';

    const metrics = [
        `[MarketCap]=>${formatNumber(agent.marketCap)}`,
        `[Price]=>${formatNumber(agent.price)}$`,
        `[Liquidity]=>${formatNumber(agent.liquidity)}`
    ].join(' | ');

    const stats = [
        `Volume last 24 hours : ${formatNumber(agent.volume24Hours)}`,
        `~Impression Count : ${formatNumber(agent.averageImpressionCount)}`,
        `~Engagements Count : ${formatNumber(agent.averageEngagementsCount)}`,
        `[Followers]=>${agent.followersCount || 'N/A'}`
    ].join('\r\n');

    return [
        `AI Agent Name : ${agent.agentName || 'N/A'} => @${twitterHandle}`,
        metrics,
        stats
    ].join('\r\n');
};

export const getTrendingAgentsAction: Action = {
    name: "COOKIE_GET_TRENDING",
    similes: [
        "TWITTER_AGENT",
        "BEST_AI_AGENT",
    ],
    description: "Get trending AI agents",
    validate: async (runtime: IAgentRuntime) => {
        await validateCookieConfig(runtime);
        return true;
    },
    handler: async (
        runtime: IAgentRuntime,
        message: Memory,
        state: State,
        _options: { [key: string]: unknown },
        callback: HandlerCallback
    ) => {
        const config = await validateCookieConfig(runtime);
        const cookieService = createCookieService(config.COOKIE_API_KEY);

        try {
            const trendingAgents = await cookieService.getTradingAgents();
            elizaLogger.success("Successfully fetched Trending Agents");

            if (callback && trendingAgents?.ok?.data) {
                const formattedAgents = trendingAgents.ok.data
                    .map(formatAgentMetrics)
                    .join("\r\n\r\n");

                callback({
                    text: `Here are actual trending ai agents:\r\n${formattedAgents}`,
                });
                return true;
            }
            return false;
        } catch (error: any) {
            elizaLogger.error("Error in Cookie plugin handler:", error);
            callback({
                text: `Error fetching Trending Agents: ${error.message}`,
                content: { error: error.message },
            });
            return false;
        }
    },
    examples: getTrendingAgentsExamples as ActionExample[][],
} as Action;
